.menu {
  height: 100%;
  width: 100%;
  position: absolute;
  position: fixed;

  /* box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15); */
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  justify-content: end;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}

.menu_is-open {
  display: flex;
}

.menu__content {
  height: 100vh;
  width: 520px;
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* animation-name: exit-menu;
  animation-duration: 0.5s; */
}

/* @keyframes exit-menu {
  from {
    width: 0px;
  } to {
    width: 520px;
  }

} */


.menu__navigation {
  text-decoration: none;
  border: none;
  background-color: #fafafa;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 27px;

}


  .menu__close {
    background-image: url("../../images/Group.png");
    height: 30px;
    width: 23px;
    background-size: cover;
    align-self: end;
    border: none;
    background-color: #fafafa;
    margin-top: 23px;
    margin-right: 30px;
    margin-bottom: 105px;
}

.menu__buttom-akkaunt {
  box-sizing: border-box;
  border: 2px solid rgba(170, 170, 170, 0.2);
  border-radius: 50px;
  background: #fafafa;
  display: flex;
  align-items: center;
  padding-left: 12px;
  min-height: 32px;
  margin-top: 62vh;
}

.menu__buttom-akkaunt-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  margin-left: 8px;
  margin-right: 5px;
}

@media (max-width: 500px) {
  .menu__close {
    margin-top: 15px;
    margin-right: 21px;
    margin-bottom: 99px;
}

.menu__navigation {
  margin-bottom: 22px;
}

.menu__buttom-akkaunt {
  margin-top: 44vh;
}

}