.movies {
  font-family: "Inter";
  font-style: normal;
}

.movies__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 70px;
  padding-right: 70px;
  margin-top: 64px;
}

@media (max-width: 800px) {
  .movies__container {
    margin-top: 80px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-width: 500px) {
  .movies__container {
    margin-top: 93px;
    padding-left: 15px;
    padding-right: 15px;
}

}
